<template>
  <div>
    <v-row class="help-form">
      <v-col class="py-0">
        <h1 class="titulo">Descreva sua dúvida</h1>
      </v-col>
      <v-col cols="12" class="py-0 mt-4">
        <textarea-field
          label=""
          :outlined="true"
          :rows="3"
          :hideDetails="true"
          @onKeyUp="setText"
        />
      </v-col> 
    </v-row>
    <v-row>
      <v-spacer></v-spacer> 
      <v-col cols="12" sm="auto" class="py-0 mt-4">
        <btn-secondary 
          text="enviar"
          :loading="loading"
          @clicked="send"
          :disabled="text.length == 0"
        />  
      </v-col>
    </v-row>
  </div>
</template>

<script>
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'
import SnackbarMixin from '@/mixins/SnackbarMixin'
export default {
  components: { BtnSecondary, TextareaField },
  mixins: [ SnackbarMixin ],
  data: () => ({
    text: '',
    loading: false
  }),
  methods: {
    send () {
      this.loading = true
      this.$axios.post(`/help`, { text: this.text })
      .then(response => {
        if (response.data.codigo === 200) {
          const text = 'Sua mensagem foi encaminhada com sucesso!'
          this.showSnackbar(text)
          this.text = ''
          this.$emit('onSuccess')
        } else this.setError('Não foi possível enviar sua mensagem!')
      }).catch (() => this.setError('Não foi possível enviar sua mensagem!'))
      .finally(() => this.loading = false)
    },
    setError (msg) {
      this.showSnackbarErro(msg)
      this.$emit('onError')
    },
    setText (text) {
      this.text = text
    }
  }
}
</script>

<style scoped>
.titulo { font-size: .8rem; }
</style>