<template>
  <div class="help">
    <v-menu
      v-model="menu"
      top
      :offset-y="true"
      :close-on-click="false"
      :close-on-content-click="false"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="app-help text-none"
          dark
          fixed
          bottom
          right
          fab
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>{{ icon }}</v-icon>
          <span class="ml-2">
            Ajuda	
          </span>
        </v-btn>
      </template>
      <div class="form">
        <card>
          <help-form @onSuccess="closeDialog"/>
        </card>
      </div>
    </v-menu>
  </div>
</template>

<script>
import Card from '@/components/globais/Card'
import HelpForm from '@/components/admin/HelpForm'
export default {
  components: { Card, HelpForm },
  data: () => ({
    menu: false
  }),
  computed: {
    icon () {
      return this.menu ? 'close' : 'help_outline'
    }
  },
  methods: {
    closeDialog () {
      this.menu = false
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/mixin.scss';
@import 'src/scss/theme.scss';

.app-help {
  background: #009688 !important;
  position: fixed;
  z-index: 5;
  bottom: 32px;
  width: auto;
  padding: 12px 28px;
  height: auto;
  font-size: 1rem;
  @include border-radius-mixin (45px);
  @include transition-mixin (all, .5s);
}
.form { width: 300px; max-width: 100%; background: transparent !important;}
@media all and (max-width: 959px) {
  .app-help { display: none; }
}
</style>