<template>
  <v-select
    v-model="valueModel"
    :label="label"
    :disabled="disabled"
    :items="items"
    item-text="text"
    item-value="value"
    :placeholder="placeholder"
    :prepend-inner-icon="icon"
    :rules="rules"
    :outlined="outlined"
    :dense="dense"
    :hide-details="hideDetails"
    @change="onChange"
    validate-on-blur
    required
  >
    <template v-slot:prepend-inner>
      <v-icon
        class="material-icons-outlined"
        :class="success ? 'success--text' : ''"
        dense
      >
        {{ icon }}
      </v-icon>
    </template>
  </v-select>
</template>

<script>
export default {
  name: 'SelectField',
  props: {
    id: { default: null },
    label: { default: null },
    placeholder: { default: null },
    value: { default: '' },
    disabled: { default: false },
    required: { default: false },
    icon: { default: null },
    success: { default: null },
    items: { default: Array },
    rules: { default: Array },
    outlined: { default: false },
    dense: { default: false },
    hideDetails: { default: false }
  },
  data: () => ({
    valueModel: ''
  }),
  watch: {
    value (newValue) {
      this.valueModel = newValue
      this.onChange()
    }
  },
  mounted () {
    this.valueModel = this.value
  },
  methods: {
    onChange () {
      this.$emit('onChange', this.valueModel, this.id)
    }
  }
}
</script>